}

.subscription__label--radio {
  display: block;
  position: relative;
  padding-left: 28px;
}
.subscription__label--radio::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  width: 20px;
  height: 20px;
  transform: translateY(-50%);
  border: 1px solid #e1e6ed;
  border-radius: 50%;
}

.subscription__input-radio:checked + .subscription__label--radio::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  width: 12px;
  height: 12px;
  background-color: #192b3f;
  border-radius: 50%;
}

.subscription__input-radio:hover + .subscription__label--radio::before,
.subscription__input-radio:focus + .subscription__label--radio::before {
  opacity: 0.4;
}

.subscription__input-radio:active + .subscription__label--radio::before {
  opacity: 1;
}

.subscription__input-radio:disabled + .subscription__label--radio {
  opacity: 0.25;
}

.subscription__label--checkbox {
  display: block;
  margin-bottom: 16px;
  padding-left: 28px;
  position: relative;
