.subscription__label--checkbox::before {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  top: 0;
  left: 0;
  background-color: #ffffff;
  border: 1px solid #e1e6ed;
  border-radius: 1px;
  background-repeat: no-repeat;
  background-position: center;
}

.subscription__input-checkbox:checked + .subscription__label--checkbox::before,
.subscription__input-checkbox:active + .subscription__label--checkbox::before {
  border: 1px solid #192b3f;
  background-color: #192b3f;
  background-image: url("data:image/svg+xml,%3Csvg width='10' height='9' viewBox='0 0 10 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10 .795 8.847 0 3.536 7.08.856 5.085 0 6.15 3.863 9 10 .795Z' fill='%23fff'/%3E%3C/svg%3E");
}

.subscription__input-checkbox:not(:checked):hover + .subscription__label--checkbox::before,
.subscription__input-checkbox:not(:checked):focus + .subscription__label--checkbox::before {
  border: 1px solid #192b3f;
  background-color: #ffffff;
  background-image: url("data:image/svg+xml,%3Csvg width='10' height='9' viewBox='0 0 10 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10 .795 8.847 0 3.536 7.08.856 5.085 0 6.15 3.863 9 10 .795Z' fill='%23E1E6ED'/%3E%3C/svg%3E");
  opacity: 1;
}

.subscription__input-checkbox:hover + .subscription__label--checkbox::before,
.subscription__input-checkbox:focus + .subscription__label--checkbox::before {
  opacity: 0.8;
}

.subscription__input-checkbox:disabled + .subscription__label--checkbox::before {
  border: 1px solid #192b3f;
  background-color: #192b3f;
  opacity: 0.25;
  background-image: none;
  pointer-events: none;
}

